@import "normalize";
@import "vars";
@import "default";
@import "fresco";
@import "aos";


@font-face {
  font-family: 'Montserrat-Bold';
  src: url("../../fonts/Montserrat-Bold.ttf") format("truetype");
}

@font-face {
  font-family: 'Montserrat-Light';
  src: url("../../fonts/Montserrat-Light.ttf") format("truetype");
}

@font-face {
  font-family: 'Montserrat-Medium';
  src: url("../../fonts/Montserrat-Medium.ttf") format("truetype");
}

@font-face {
  font-family: 'Montserrat-Regular';
  src: url("../../fonts/Montserrat-Regular.ttf") format("truetype");
}

@font-face {
  font-family: 'Montserrat-SemiBold';
  src: url("../../fonts/Montserrat-SemiBold.ttf") format("truetype");
}
body, .customize-support{
  margin: 0!important;
  padding: 0;
}
button.slick-arrow{
  color: transparent;
  background-color: transparent;
  width: 64px;
  position: absolute;
  border: none;
  z-index: 20;
  &.slick-next{
    right: -64px;
    width: 44px;
    height: 44px;
    background-repeat: no-repeat;
    background-size: contain;
    background-position: center;
    top: calc(50% - 22px);
    background-image: url("image/next-swiper.svg");
  }
  &.slick-prev{
    left: -64px;
    width: 44px;
    height: 44px;
    background-repeat: no-repeat;
    background-size: contain;
    background-position: center;
    top: calc(50% - 22px);
    background-image: url("image/prev-swiper.svg");
  }
  &.slick-disabled{
    opacity: 0.35;
  }
}

a {
  text-decoration: none;
}

* {
  box-sizing: border-box;
  outline: none !important;
  -webkit-tap-highlight-color: transparent;
}

body {
  margin: 0;
  width: 100%;
  position: relative;
  @media all and (max-width: 1180px) {
    &:before{
      content: "";
      display: flex;
      flex-direction: column;
      height: 342px;
      background: linear-gradient(180deg, rgba(164, 244, 255, 0.576042) 0%, rgba(255, 255, 255, 0) 100%);
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      z-index: 1;
      pointer-events: none;
      @media all and (max-width: 900px) {
        height: 161px;
      }
    }
  }
}
.mobile-menu{
  display: none;
  background-color: #F8E688;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.2);
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 48px;
  padding: 10px 20px 10px 80px;
  align-items: center;
  z-index: 99997;
  &__logo{
    position: absolute;
    top: 4px;
    left: 20px;
    img, svg{
      width: auto;
      height: 40px;
    }
  }
  &__socials{
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 12px;
  }
  .close-nav-mobile{
    cursor: pointer;
  }
  .footer__socials{
    width: 150px;
    .socials__wrapper{
      width: 100%;
      display: flex;
      justify-content: space-between;
      align-items: center;
    }
  }
  &__nav{
    position: fixed;
    top: 0;
    right: -100%;
    width: 100%;
    height: 100vh;
    overflow: scroll;
    padding: 20px;
    background-color: #F8E688;
    z-index: 999999;
    transition: 0.3s;
    .searchform{
      top: 0;
      position: relative;
      margin: 0 0 18px 0;
      .search-btn{
        background-color: #F1D333;
      }
    }
    &.show-menu{
      right: 0!important;
    }
  }
  @media all and (max-width: 1180px) {
    display: flex;
    justify-content: space-between;
  }
  &__burger{
    display: flex;
    flex-direction: column;
    cursor: pointer;
    justify-content: space-between;
    width: 33px;
    height: 28px;
    span{
      display: block;
      background-color: black;
      height: 2px;
      width: 100%;
    }
  }
  .menu-custom-devider{
    color: transparent;
    width: 100%;
    display: block;
    &:before{
      height: 1px;
      content: "";
      display: block;
      width: 100%;
      margin: 10px 0;
      background-color: black;
    }
    a{
      display: none!important;
    }
  }
  .menu-custom{
    padding: 0;
    a{
      display: flex!important;
      align-items: center;
      &:hover{
        transform: scale(1) !important;
      }
      &:before{
        content: "";
        display: block;
        width: 28px;
        height: 28px;
        background-size: contain;
        background-repeat: no-repeat;
        background-position: center;
        margin-right: 10px;
      }
    }

  }
  .menu-custom-zakaz{
    a{
      &:before{
        background-image: url(image/icons/zakaz.svg);
      }
    }
  }
  .menu-custom-opt{
    a{
      &:before{
        background-image: url(image/icons/opt.svg);
      }
    }
  }
  .menu-custom-pay{
    a{
      &:before{
        background-image: url(image/icons/pay.svg);
      }
    }
  }
  .menu-custom-dost{
    a{
      &:before{
        background-image: url(image/icons/dost.svg);
      }
    }
  }
  .menu-custom-gar{
    a{
      &:before{
        background-image: url(image/icons/gar.svg);
      }
    }
  }
  .menu-custom-about{
    a{
      &:before{
        background-image: url(image/icons/about.svg);
      }
    }
  }
  .menu-custom-prod{
    a{
      &:before{
        background-image: url(image/icons/prod.svg);
      }
    }
  }
  .menu-custom-eko{
    a{
      &:before{
        background-image: url(image/icons/tur.svg);
      }
    }
  }
  .menu-custom-news{
    a{
      &:before{
        background-image: url(image/icons/news.svg);
      }
    }
  }
  .menu-custom-rev{
    a{
      &:before{
        background-image: url(image/icons/rev.svg);
      }
    }
  }
  .menu-custom-contacts{
    a{
      &:before{
        background-image: url(image/icons/cont.svg);
      }
    }
  }
  .menu-custom-devider menu-custom{

  }
  ul.menu{
    display: flex;
    flex-direction: column;
    margin: 0;
    list-style: none;
    .menu-item{
      a{
        width: auto;
        font-family: Montserrat-Regular;
        font-size: 18px;
        line-height: 22px;
        color: #000000;
        margin-left: 0;
      }
    }
  }
}
.section-title{
  font-size: 48px;
  line-height: 59px;
  font-family: Montserrat-Medium;
  color: #000;
  text-align: center;
  max-width: 930px;
  margin: auto;
  padding: 0 50px;
  @media all and (max-width: 900px) {
    font-size: 40px;
    margin: 15px 0 15px 0;
  }
  @media all and (max-width: 768px) {
    font-size: 36px;
    line-height: 100%;
  }
  @media all and (max-width: 500px) {
    font-size: 24px;
    margin-top: 15px;
    font-family: Montserrat-Bold;
  }
}
.icons-account-menu{
  #icons-menu{
    padding-top: 7px;
    margin-bottom: 0;
    margin-left: 0;
    @media all and (max-width: 1180px) {
      padding-top: 0;
      display: flex;
      flex-direction: row;
      li{
        margin-left: 15px;
      }
    }
  }
  .icons-account-list{
    width: 100%;
    @media all and (max-width: 1180px) {
      width: auto;
    }
  }
  .icons-account-menu__korzina a, .menu-item a{
    width: 46px;
    height: 40px;
    color: transparent!important;
    overflow: hidden;
    display: block;
    @media all and (max-width: 1180px) {
      height: 31px;
      width: 36px!important;
      margin-left: 15px;
    }
  }
  ul.menu{
    display: flex;
    justify-content: space-between;
    list-style: none;
    @media all and (max-width: 1400px) {
      justify-content: center;
    }
    .menu-item{
      margin-bottom: 0;
      display: flex;
      align-items: center;
      justify-content: center;
      @media all and (max-width: 1400px) {
        margin: 0 10px;
      }
      @media all and (max-width: 1180px) {
        margin: 0;
      }
      a{
        background-repeat: no-repeat;
        background-size: contain;
        background-position: center;
        transition: 0.3s;
        @media all and (max-width: 1480px) {
          width: 32px;
          height: auto;
        }
        @media all and (max-width: 1180px) {
          width: 32px;
          height: 31px;
        }
        &:hover{
          transform: scale(1.2);
        }
      }
    }
    .logout{
      a{
        background-image: url(image/out.svg);
      }
    }
    .login{
      a{
        background-image: url(image/account.svg);
      }
    }
    .korzina{
      a{
        background-image: url(image/rec.svg);
      }
    }
    .wishlist{
      a{
        background-image: url(image/with.svg);
        background-repeat: no-repeat;
        background-size: contain;
        background-position: center;
      }
    }
  }
}
.searchform{
  margin-top: 15px;
  position: absolute;
  top: -100%;
  left: 0;
  width: 100%;
  display: flex;
  .input-search{
    border: none;
    border-radius: 8px 0 0 8px;
    padding: 10px;
    width: calc(100% - 45px);
    flex: 1;
  }
  .input-btn{
    display: none;
  }
  .search-btn{
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #F8E688;
    border-radius: 0 8px 8px 0;
    width: 45px;
    height: 100%;
    padding: 5px;
    cursor: pointer;
    transition: 0.3s;
    svg{
      width: 32px;
      height: 32px;
    }
    &:hover{
      background-color: #F1D333;
      svg{

      }
    }
  }
}
.header{
  display: flex;
  flex-direction: column;
  align-items: center;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 2;
  @media all and (max-width: 1180px) {
    display: none;
  }
  .mobile-menu{
    display: none;
  }
  &:before{
    content: "";
    display: flex;
    flex-direction: column;
    height: 342px;
    background: linear-gradient(180deg, rgba(164, 244, 255, 0.576042) 0%, rgba(255, 255, 255, 0) 100%);
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    z-index: -1;
    pointer-events: none;
  }
  .under-nav{
    display: flex;
    justify-content: center;
    background: #F8E688;
    box-shadow: 0 4px 4px rgba(0, 0, 0, 0.2);
    border-radius: 0 0 40px 40px;
  }
  &__row{
    display: flex;
    justify-content: center;
    width: 100%;
    padding: 0 100px;
    @media all and (max-width: 1480px) {
