  &__branding, &__contacts{
    width: 20%;
    max-width: 200px;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    position: relative;
  }
  &__branding{
    .custom-logo-link{
      img{
        width: 100%;
        height: auto;
      }
