        border: none;
        border-radius: 8px 0 0 8px;
        padding: 10px;
        width: calc(100% - 45px);
        flex: 1;
      }
      .input-btn{
        display: none;
      }
      .search-btn{
        display: flex;
        justify-content: center;
        align-items: center;
        background-color: #F8E688;
        border-radius: 0 8px 8px 0;
        width: 45px;
        height: 100%;
        padding: 5px;
        cursor: pointer;
        transition: 0.3s;
        svg{
          width: 32px;
          height: 32px;
        }
        &:hover{
          background-color: #F1D333;
          svg{

          }
        }
      }
