  &__contacts{
    .icons-account-menu{
      position: absolute;
      top: -100%;
      right: 0;
      width: 100%;
      display: flex;
      justify-content: space-between;
      @media all and (max-width: 1400px) {
        justify-content: center;
      }
      .fix_cart_count{
        font-size: 48px;
        line-height: 59px;
        font-family: Montserrat-Medium;
        text-align: center;
        color: #7a99a9;
      }
    }
    a{
      font-family: Montserrat-SemiBold;
      font-weight: 600;
      font-size: 22px;
      line-height: 27px;
      color: $darkcolor;
      @media all and (max-width: 1400px) {
        font-size: 16px;
        padding: 0 10px;
      }
    }
    align-items: center;
  }
  &__btn{
    cursor: pointer;
    span{
      font-size: 20px;
      line-height: 24px;
      font-family: Montserrat-Regular;
      color: $darkcolor;
      transition: 0.3s;
      border-bottom: 1px solid transparent;
      &:hover{
        color: #4F630E;
        border-bottom: 1px solid #4F630E;
      }
    }
  }
}
#main-menu{
  display: flex;
  justify-content: center;
  list-style: none;
  margin-bottom: 0;
  margin-left: 0;
  .menu-item{
    margin-bottom: 0;
    a{
      font-family: Montserrat-Medium;
      font-style: normal;
      font-weight: 500;
      font-size: 20px;
      line-height: 24px;
      color: $darkcolor;
      margin: 0 18px;
      position: relative;
      overflow: hidden;
      display: inline-block;
      @media all and (max-width: 1400px) {
        font-size: 16px;
        margin: 0 10px;
      }
      &:hover{
        &:before{
          transform: translateX(0);
        }
      }
      &:before{
        transition: 0.5s;
        position: absolute;
        bottom: 0;
        left: 0;
        content: "";
        display: block;
        width: 100%;
        height: 2px;
        background-color: #000000;
        transform: translateX(-101%);
      }
    }
  }
}
#additional-menu{
  display: flex;
  justify-content: center;
  padding: 0 33px;
  list-style: none;
  margin-bottom: 0;
  margin-left: 0;
  @media all and (max-width: 1400px) {
    padding: 0;
  }
  .menu-item{
    margin: 26px 23px 30px 23px ;
    @media all and (max-width: 1400px) {
      font-size: 16px;
      margin: 26px 10px;
    }
    a{
      font-family: Montserrat-Regular;
      font-size: 18px;
      line-height: 22px;
      text-transform: lowercase;
      color: $darkcolor;
      transition: 0.3s;
      border-bottom: 1px solid transparent;
      @media all and (max-width: 1400px) {
        font-size: 16px;
        padding: 0 10px;
      }
      &:hover{
        color: #4F630E;
        border-bottom: 1px solid #4F630E;
      }
    }
  }
}
.main-page-content{
  padding-top: 274vw * $px;
  background-size: contain;
  background-repeat: no-repeat;
  background-position: bottom center;
  min-height: 100vh;
  position: relative;
  @media all and (max-width: 1180px) {
    height: auto;
    min-height: auto;
    padding-bottom: 500vw * $px;
  }
  @media all and (max-width: 500px) {
    padding-bottom: 153px;
    padding-top: 175px;
  }
  &__title{
    font-weight: 600;
    font-size: 64px;
    line-height: 78px;
    font-family: Montserrat-SemiBold;
    max-width: 840px;
    margin: auto;
    text-align: center;
    z-index: 3;
    position: relative;
    color: $darkcolor;
    @media all and (max-width: 1180px) {
      font-size: 58px;
    }
    @media all and (max-width: 900px) {
      font-size: 48px;
      width: 80%;
      line-height: 120%;
    }
    @media all and (max-width: 768px) {
      font-size: 38px;
    }
    @media all and (max-width: 500px) {
      font-size: 27px;
    }
  }
  &__subtitle{
    font-family: Montserrat-Medium;
    font-size: 30px;
    line-height: 37px;
    color: $darkcolor;
    text-align: center;
    @media all and (max-width: 500px) {
      font-size: 16px;
    }
  }
}
.family-farm{
  margin: 30px auto 0 auto;
  max-width: 1250px;
  padding: 0 100px;
  text-align: center;
  @media all and (max-width: 1480px) {
    padding: 0 50px;
  }
  @media all and (max-width: 900px) {
    text-align: right;
  }
  @media all and (max-width: 768px) {
    padding: 0 20px;
    margin-top: 0;
  }
  &.eko-page{
    .family-farm__columns{
      margin: 30px 0 60px 0;
    }
  }
  &__title{
    color: $darkcolor;
    text-align: center;
  }
  &__columns{
    display: flex;
    justify-content: space-between;
    margin-top: 30px;
    flex-wrap: wrap;
    @media all and (max-width: 768px) {
      margin-top: 10px;
    }
  }
  &__column{
    width: calc(50% - 50px);
    @media all and (max-width: 900px) {
      width: calc(50% - 15px);
    }
    @media all and (max-width: 768px) {
      width: 100%;
      &:last-child{
        p{
          &:last-child{
            margin-bottom: 0;
          }
        }
      }
    }
    p{
      font-weight: 300;
      font-size: 18px;
      line-height: 28px;
      font-family: Montserrat-Light;
      color: $darkcolor;
      text-align: left;
      @media all and (max-width: 900px) {
        font-size: 16px;
        line-height: 170%;
      }
      @media all and (max-width: 768px) {
        margin-bottom: 30px;
      }
      @media all and (max-width: 500px) {
        br{
          display: none;
        }
      }
    }
  }
  &__more{
    display: inline-block;
    margin: 40px auto 40px auto;
    font-weight: 500;
    font-size: 24px;
    line-height: 29px;
    color: $darkcolor;
    font-family: Montserrat-Medium;
    @media all and (max-width: 1024px) {
      margin: 10px auto;
      font-size: 16px;
    }
    @media all and (max-width: 768px) {
      font-size: 14px;
      line-height: 100%;
    }
    .arrow-right {
      cursor: pointer;
      position: relative;
      width: 100%;
      height: 4px;
      margin: 10px 0 0 0;
      @media all and (max-width: 1024px) {
        margin: 4px 0 0 0;
      }

    }
    .arrow-right div {
      position: relative;
      top: 0;
      width: 100%;
      height: 1px;
      background-color: $darkcolor;
      box-shadow: 0 3px 5px rgba(0, 0, 0, .2);
      left:0;
      display: block;
    }
    .arrow-right div::after {
      content: '';
      position: absolute;
      width: 4px;
      height: 1px;
      top: -2px;
      right: 0;
      background-color: $darkcolor;
      transform: rotate(45deg);
    }
    .arrow-right div::before {
      content: '';
      position: absolute;
      width: 4px;
      height: 1px;
      top: 2px;
      right: 0;
      background-color: $darkcolor;
      box-shadow: 0 3px 5px rgba(0, 0, 0, .2);
      transform: rotate(-45deg);
    }
  }
}
.products-page{
  margin-bottom: 80px;
}
.products{
  max-width: 1250px;
  padding: 0 100px;
  margin: 30px auto;
  @media all and (max-width: 768px) {
    padding: 0 20px;
  }
  @media all and (max-width: 500px) {
    padding: 0;
  }
  &__list{
    .products{
      display: flex!important;
      justify-content: space-between!important;
      flex-wrap: wrap!important;
      @media all and (max-width: 768px) {
        justify-content: center!important;
      }
      &::before, &::after{
        display: none!important;
      }
      .product-category{
        width: calc(33.3% - 50px) !important;
        text-align: center;
        margin: 35px 0 0 0!important;
        @media all and (max-width: 1024px) {
          width: calc(33.3% - 20px) !important;
        }
        @media all and (max-width: 768px) {
          width: calc(50% - 20px) !important;
          padding: 0 20px!important;
          display: flex;
          flex-direction: column;
          justify-content: flex-end;
        }
        > a img{
          height: 213vw * $px!important;
          width: auto!important;
          margin: auto!important;
          transition: 0.3s;
          @media all and (max-width: 1024px) {
            height: 120vw * $pxmt!important;
            width: auto;
          }
          @media all and (max-width: 768px) {
            height: auto!important;
            width: 20.927vw!important;
          }
          @media all and (max-width: 400px) {
            height: auto!important;
            width: 25.667vw!important;
          }
          &:hover{
            transform: scale(1.05);
          }
        }
        h2{
          max-width: 190px;
          text-align: center;
          font-weight: 600;
          font-size: 18px;
          line-height: 22px;
          color: $darkcolor;
          font-family: Montserrat-SemiBold;
          margin: 20px auto 0 auto!important;
          text-transform: uppercase;
          @media all and (max-width: 1024px) {
            font-size: 14px!important;
          }
          @media all and (max-width: 768px) {
            font-size: 14px!important;
            min-height: 54px;
            display: flex;
            align-items: center;
            justify-content: center;
          }
          @media all and (max-width: 400px) {
            font-size: 10px!important;
          }
          .count{
            display: none;
          }
